import React, { useState, useEffect } from 'react';
import ProductsPxp from '../../../_pxp/components/Products/ProductsPxp';
import DialogPxp from '../../../_pxp/components/DialogPxp';
import ProfileGirl from '../Profile/ProfileGirl';
import Pxp from '../../../Pxp';
import SetDate from '../Profile/SetDate';
import useLocationPxp from '../../../_pxp/hooks/useLocationPxp';
import { getUrlForView } from '../../../_pxp/utils/Common';

const Discover = () => {
  const { position } = useLocationPxp();
  const localgeo = JSON.parse(localStorage.getItem('currentLocation'));
  console.log('LOCATION', position);

  const [profile, setProfile] = useState({
    open: false,
    idGirl: undefined,
    alias: undefined,
  });
  const [openSetDate, setOpenSetDate] = useState(false);
  const [girl, setGirl] = useState({ idGirl: undefined, alias: undefined });

  const handleOpenSetDate = (item) => {
    console.log(item);
    setOpenSetDate(true);
    setGirl({ idGirl: item.id_dama, alias: item.alias });
  };
  const handleCloseSetDate = () => {
    setOpenSetDate(false);
  };
  const handleOpenProfile = (item) => {
    setProfile({
      open: true,
      idGirl: item.id_dama,
      alias: item.alias,
    });
  };
  const handleCloseProfile = () => {
    setProfile({
      open: false,
    });
  };

  const handleChangeLike = (id, callback) => {
    Pxp.apiClient
      .doRequest({
        url: 'citas/Dama/cambiarMeGustaDama',
        params: {
          id_dama: id,
        },
      })
      .then((resp) => {
        callback();
      });
  };

  const filters = [
    {
      label: 'disponible',
      value: 'disponible',
      field: 'filtrar_dama',
      criteria: 'equal', // default equal
    },
    {
      label: 'Favorito',
      value: 'favorito',
      field: 'filtrar_dama',
      criteria: 'greater',
    },
    {
      label: 'Todos',
      value: 'todos',
      field: 'filtrar_dama',
      criteria: 'equal',
    },
    {
      field: 'dama.descripcion#u.alias',
      search: true,
      criteria: 'equal',
    },
  ];

  const config = {
    idStore: 'id_dama',
    getDataTable: {
      url: 'citas/Dama/listarDamaFiltros',
      params: {
        sort: 'u.id_usuario',
        limit: 50,
        latitud: position?.lat || localgeo?.lat || '',
        longitud: position?.lng || localgeo?.lng || '',
      },
      load: false,
    },
    columns: {
      title: 'alias',
      subtitle: 'edad',
      active: (item) => item.disponible === 'si',
      description: 'descripcion',
      rating: 'promedio_calificacion',
    },
    actions: {
      perfil: {
        icon: 'person_pin',
        color: 'secondary',
        action: ({ item }) => handleOpenProfile(item),
      },
      favorite: {
        icon: (item) => {
          // if the icon is a function then we need to to some logic for return the icon
          if (parseInt(item.me_gusta, 10) > 0) {
            return 'favorite';
          }
          return 'favorite_border';
        },
        action: ({ item, setStateActions }) => {
          handleChangeLike(item.id_dama, () => {
            setStateActions((prevData) => {
              let aux = '';
              if (typeof prevData.favorite.icon === 'function') {
                aux = prevData.favorite.icon(item);
              } else {
                aux = prevData.favorite.icon;
              }

              const newIcon =
                aux === 'favorite' ? 'favorite_border' : 'favorite';

              return {
                ...prevData,
                favorite: {
                  ...prevData.favorite,
                  icon: newIcon,
                },
              };
            });
          });
        },
      },
      send: {
        icon: 'send',
        color: 'secondary',
        action: ({ item }) => handleOpenSetDate(item),
      },
    },
    features: [
      {
        label: 'Precio',
        field: 'precio_hora',
        unit: 'Bs',
        pipe: (item) =>
          Math.round((parseFloat(item.precio_hora) + Number.EPSILON) * 100) /
          100,
      },
      {
        label: 'Distancia',
        field: 'distancia',
        unit: 'Km',
        pipe: (item) =>
          Math.round((parseFloat(item.distancia) + Number.EPSILON) * 100) / 100,
      },
    ],
    urlImage: (item) =>
      getUrlForView({
        nameFile: item.nombre_archivo,
        extension: item.extension,
        folder: item.folder,
        size: '',
      }),
  };

  return (
    <div>
      {!position.error && <ProductsPxp config={config} filters={filters} />}
      {position.error && <h3>{position.error}</h3>}
      <DialogPxp
        titleToolbar={profile.alias}
        onClose={handleCloseProfile}
        open={profile.open}
      >
        <ProfileGirl idGirl={profile.idGirl} />
      </DialogPxp>
      <DialogPxp
        titleToolbar={`Fijar Cita con ${girl.alias}`}
        onClose={handleCloseSetDate}
        open={openSetDate}
      >
        <SetDate idGirl={girl.idGirl} handleCloseSetDate={handleCloseSetDate} />
      </DialogPxp>
    </div>
  );
};
export default Discover;
