import React from 'react';
import makeStyles from '@material-ui/core/styles/makeStyles';
import CustomMap from '../../../_pxp/components/GoogleMaps/CustomMap';

const MapStyle = {
  bigMap: {
    height: '90vh',
    maxHeight: '90vh',
    width: '100%',
    display: 'block',
  },
};

const useStyles = makeStyles(MapStyle);

const Map = ({ lat, lng }) => {
  React.useEffect(() => {
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
  });
  const classes = useStyles();
  return (
    <div>
      <div className={classes.bigMap}>
        <CustomMap
          lat={lat}
          lng={lng}
          googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_MAPS_KEY}`}
          loadingElement={<div style={{ height: `100%` }} />}
          containerElement={
            <div
              style={{
                height: `100%`,
                borderRadius: '6px',
                overflow: 'hidden',
              }}
            />
          }
          mapElement={<div style={{ height: `100%` }} />}
        />
      </div>
    </div>
  );
};

export default Map;
