import React, { useEffect, useState } from 'react';
import classNames from 'classnames';
import { makeStyles, useTheme } from '@material-ui/core/styles';

import Favorite from '@material-ui/icons/Favorite';
import { Scrollbars } from 'react-custom-scrollbars';
import Tooltip from '@material-ui/core/Tooltip';
import Rating from '@material-ui/lab/Rating';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import FavoriteIcon from '@material-ui/icons/Favorite';
import Box from '@material-ui/core/Box';
import profilePageStyle from '../../../_assets/jss/view/profilePageStyle';
import Parallax from '../../../_pxp/components/Parallax/Parallax';

import GridContainer from '../../../_pxp/components/Grid/GridContainer';
import GridItem from '../../../_pxp/components/Grid/GridItem';
import Clearfix from '../../../_pxp/components/Clearfix/Clearfix';
import CustomButton from '../../../_pxp/components/CustomButtons/Button';
import ImageGallery from '../../../_assets/icons/ImageGallery';
import GoogleMaps from '../../../_assets/icons/GoogleMaps';
import HealthCalendar from '../../../_assets/icons/HealthCalendar';
import useJsonStore from '../../../_pxp/hooks/useJsonStore';
import { getUrlForView } from '../../../_pxp/utils/Common';
import GridListImage from '../../../_pxp/components/GridListImage/GridListImage';
import DialogPxp from '../../../_pxp/components/DialogPxp';
import SetDate from './SetDate';
import Pxp from '../../../Pxp';
import Map from './Map';

const useStyles = makeStyles(profilePageStyle);

const ProfileGirl = ({ idGirl }) => {
  const theme = useTheme();
  const classes = useStyles();
  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid,
  );
  const [girl, setGirl] = useState();
  const [openGridListImage, setOpenGridListImage] = useState(false);
  const [openSetDate, setOpenSetDate] = useState(false);
  const [openMap, setOpenMap] = useState(false);
  const [like, setLike] = useState();
  const getDataTable = {
    url: 'citas/Dama/getDamaSelected',
    params: {
      id_dama: idGirl,
    },
  };
  const { data, setData } = useJsonStore(getDataTable);
  const [init, setInit] = useState(false);

  const handleChangeLike = () => {
    Pxp.apiClient
      .doRequest({
        url: 'citas/Dama/cambiarMeGustaDama',
        params: {
          id_dama: girl.dama.id_dama,
        },
      })
      .then((resp) => {
        setLike(!like);
      });
  };

  // when the data has gotten an resp
  useEffect(() => {
    console.log('data', data);
    if (data) {
      const json = JSON.parse(data.data.res);
      console.log(json);
      setGirl(json);
      setInit(true);
      const isLike = json.like !== 0;
      setLike(isLike);
    }
  }, [data]);
  // mount
  useEffect(() => {}, []);

  const handleOpenGallery = () => {
    setOpenGridListImage(true);
  };
  const handleCloseGallery = () => {
    setOpenGridListImage(false);
  };
  const handleOpenSetDate = () => {
    setOpenSetDate(true);
  };
  const handleCloseSetDate = () => {
    setOpenSetDate(false);
  };
  const handleOpenMap = () => {
    setOpenMap(true);
  };
  const handleCloseMap = () => {
    setOpenMap(false);
  };

  return (
    <>
      <Scrollbars>
        {girl && (
          <div>
            <Parallax
              image={getUrlForView({
                nameFile: girl.cover_file.nombre_archivo,
                folder: girl.cover_file.folder,
                extension: girl.cover_file.extension,
              })}
              filter="dark"
              className={classes.parallax}
            >
              <GridContainer className={classes.pullRight}>
                <CustomButton color="rose" onClick={handleOpenSetDate}>
                  <HealthCalendar style={{ width: '25px', height: '25px' }} />{' '}
                  Fijar Cita
                </CustomButton>
              </GridContainer>
            </Parallax>
            <div className={classNames(classes.main, classes.mainRaised)}>
              <div className={classes.container}>
                <GridContainer justify="center">
                  <GridItem xs={12} sm={12} md={6}>
                    <div className={classes.profile}>
                      <div>
                        <img
                          src={getUrlForView({
                            nameFile: girl.perfil_file.nombre_archivo,
                            folder: girl.perfil_file.folder,
                            extension: girl.perfil_file.extension,
                          })}
                          alt="..."
                          className={imageClasses}
                        />
                      </div>
                      <div className={classes.name}>
                        <h3
                          className={classes.title}
                          style={{ marginBottom: 0 }}
                        >
                          {girl.dama.alias}
                        </h3>
                        <div
                          style={{
                            display: 'inline-flex',
                            alignItems: 'center',
                          }}
                        >
                          <Rating
                            name="simple-controlled"
                            value={girl.dama.promedio_calificacion}
                            readOnly
                            classes={{
                              iconEmpty: classes.iconEmpty,
                            }}
                          />
                          <Box ml={2}>{girl.dama.promedio_calificacion}</Box>
                        </div>
                      </div>
                    </div>
                    <div className={classes.follow}>
                      <Tooltip
                        id="tooltip-top"
                        title="Follow this user"
                        placement="top"
                        classes={{ tooltip: classes.tooltip }}
                      >
                        <CustomButton
                          justIcon
                          round
                          color="rose"
                          className={classes.followButton}
                          onClick={handleChangeLike}
                        >
                          {!like ? (
                            <FavoriteBorderIcon
                              className={classes.followIcon}
                            />
                          ) : (
                            <FavoriteIcon className={classes.followIcon} />
                          )}
                        </CustomButton>
                      </Tooltip>
                    </div>
                  </GridItem>
                </GridContainer>
                <div
                  className={classNames(
                    classes.description,
                    classes.textCenter,
                  )}
                >
                  <p>
                    {girl.dama.descripcion} {''}
                  </p>
                  <CustomButton
                    onClick={handleOpenGallery}
                    color="transparent"
                    simple
                  >
                    <ImageGallery fontSize="large" /> Fotos
                  </CustomButton>
                  <CustomButton
                    color="transparent"
                    simple
                    onClick={handleOpenMap}
                  >
                    <GoogleMaps fontSize="large" /> Mapa
                  </CustomButton>
                  <Clearfix />
                  <CustomButton
                    justIcon
                    round
                    color="rose"
                    onClick={handleOpenSetDate}
                  >
                    <HealthCalendar style={{ width: '30px', height: '30px' }} />
                  </CustomButton>
                  <Clearfix />
                  Fijar Cita
                </div>

                <Clearfix />
              </div>
            </div>
          </div>
        )}
      </Scrollbars>
      {girl && (
        <>
          <DialogPxp
            titleToolbar="Galeria"
            onClose={handleCloseGallery}
            open={openGridListImage}
          >
            <GridListImage
              idTable={girl.dama.id_dama}
              idTypeFile={girl.id_gallery}
            />
          </DialogPxp>
          <DialogPxp
            titleToolbar="Fijar Cita"
            onClose={handleCloseSetDate}
            open={openSetDate}
          >
            <SetDate
              idGirl={girl.dama.id_dama}
              handleCloseSetDate={handleCloseSetDate}
            />
          </DialogPxp>
          <DialogPxp
            titleToolbar="Fijar Cita"
            onClose={handleCloseMap}
            open={openMap}
          >
            <Map
              lat={girl.dama.disponible_latitud}
              lng={girl.dama.disponible_longitud}
            />
          </DialogPxp>
        </>
      )}
    </>
  );
};

export default ProfileGirl;
